import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { NgForm } from '@angular/forms';
import { CloudStorageProviderService } from 'src/app/services/cloud-storage-provider.service';
import { ArticlesService } from 'src/app/services/uiServices/articles.service';
declare var $: any;
import { DatepickerOptions } from 'ng2-datepicker';
import * as moment from 'moment';

@Component({
  selector: 'app-add-or-edit-article',
  templateUrl: './add-or-edit-article.component.html',
  styleUrls: ['./add-or-edit-article.component.css']
})
export class AddOrEditArticleComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  articleId: string;
  articleType: string;
  isEdit: boolean;
  selectedArticle: Article = {
    article: "",
    caption: "",
    images: [""],
    publishDate: "",
    publishTime: "",
    videos: "",
    isShowen: false,
    writer: "",
    name: ""
  };
  options: DatepickerOptions = {
    minYear: 1970,
    maxYear: 2030,
    displayFormat: 'DD-MM-YYYY',
    barTitleFormat: 'MMMM YYYY',
    dayNamesFormat: 'dd',
    firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
    maxDate: new Date(Date.now()),  // Maximal selectable date
    barTitleIfEmpty: 'انقر لاختيار التاريخ',
    placeholder: 'انقر لاختيار التاريخ', // HTML input placeholder attribute (default: '')
    fieldId: 'my-date-picker',
    useEmptyBarTitle: false,

  };
  selectedArticleImage: any;

  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private articlesService: ArticlesService,
    private spinner: NgxSpinnerService,
    private cloudStorage: CloudStorageProviderService,
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.articleId = params.id;
      this.articleType = params.type;
      this.isEdit = this.articleId != "0";
      console.log(this.articleId + " " + this.articleType);
      this.successfulDialog.options = {
        confirmButtonText: "تم"
      };
      this.errorDialog.options = {
        confirmButtonText: "حسنا"
      };
      this.getArticleDetails();
    });
  }
  async getArticleDetails() {
    if (this.isEdit) {
      try {
        this.spinner.show();
        this.selectedArticle = await this.articlesService.getArticleDetails(this.articleId, this.articleType == "sports");
        this.selectedArticle.publishTime = this.selectedArticle.publishTime.substr(this.selectedArticle.publishTime.indexOf("T") + 1, 5);
        this.spinner.hide();
        this.initEditor();
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.initEditor();
    }

  }

  async initEditor() {
    $('#summernote').summernote({
      height: 350, // set editor height
      minHeight: null, // set minimum height of editor
      maxHeight: null, // set maximum height of editor
    });
    $('.summernote').summernote({
      airMode: true
    });
    $('#summernote').code(this.selectedArticle.article);
  }
  onBackCliced() {
    this.location.back();
  }

  onSelectedFile(event: any) {
    console.log("Selected file data");
    console.log(event.target.files[0].name);
    console.log(event.target.files[0]);
    this.selectedArticleImage = event;
  }
  deleteFile() {
    this.selectedArticleImage = null;
  }

  async save(form: NgForm) {
    console.log(form);
    if (form.valid && this.selectedArticle.publishDate != "" && this.selectedArticle.publishTime != "") {
      if (this.selectedArticle.images[0] != "" || this.selectedArticleImage) {
        this.spinner.show();
        $('#summernote').destroy();
        console.log($('#summernote').code());
        this.selectedArticle.article = $('#summernote').code();
        this.selectedArticle.publishDate = moment(this.selectedArticle.publishDate).toISOString();
        this.selectedArticle.publishTime = `2015-11-22T${this.selectedArticle.publishTime}:00.000Z`;
        if (this.selectedArticleImage) {
          const imageUrl = await this.cloudStorage.uploadFile(this.selectedArticleImage, this.selectedArticleImage.target.files[0].name);
          this.selectedArticle.images = [
            imageUrl
          ];
        }
        if (this.isEdit) {
          this.articlesService.updatedArticle(this.selectedArticle, this.articleType == "sports").then((isDone) => {
            this.spinner.hide();
            this.successfulDialog.show().then(value => {
              this.onBackCliced();
            });
          },
            error => {
              this.spinner.hide();
              this.errorDialog.options = {
                title: "تأكد من اتصالك بالانترنت",
                confirmButtonText: "حسنا"
              };
              this.errorDialog.show();
            });
        }
        else {
          this.articlesService.createArticle(this.selectedArticle, this.articleType == "sports").then((isDone) => {
            this.spinner.hide();
            this.successfulDialog.show().then(value => {
              this.onBackCliced();
            });
          },
            error => {
              this.spinner.hide();
              this.errorDialog.options = {
                title: "تأكد من اتصالك بالانترنت",
                confirmButtonText: "حسنا"
              };
              this.errorDialog.show();
            });
        }

      }
      else {
        this.errorDialog.options = {
          title: "من فضلك قم باختيار صورة الموضوع",
          confirmButtonText: "حسنا"
        };
        this.errorDialog.show();
      }

    }
    else {
      this.errorDialog.options = {
        title: "من فضلك قم بملء بيانات الموضوع",
        confirmButtonText: "حسنا"
      };
      this.errorDialog.show();
    }
  }
}
