import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-use-terms',
  templateUrl: './use-terms.component.html',
  styleUrls: ['./use-terms.component.css']
})
export class UseTermsComponent implements OnInit {

  terms: any;
  settings: Settings;
  constructor(
    private sanitizer: DomSanitizer,
    private uiServicesProvider: HomePageServicesService,
    private router: Router
  ) {
    this.loadSettings();
  }
  async loadSettings() {
    this.settings = await this.uiServicesProvider.getSettings();
    this.terms = this.sanitizer.bypassSecurityTrustHtml(this.settings.infoDaTa.termsOfUse);
  }
  ngOnInit() {
  }

  edit() {
    this.router.navigateByUrl("/editTerms");
  }
}
