import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { Router } from '@angular/router';

declare var jQuery: any;
declare var $: any;
@Component({
  selector: 'app-edit-about-us',
  templateUrl: './edit-about-us.component.html',
  styleUrls: ['./edit-about-us.component.css']
})
export class EditAboutUsComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('imageValidation') private imageValidation: SwalComponent;
  aboutUs: any;
  settings: Settings;
  editorDisabled = true;
  constructor(
    private spinner: NgxSpinnerService,
    private uiServicesProvider: HomePageServicesService,
    private router: Router
  ) { }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: "تم"
    };
    this.errorDialog.options = {
      confirmButtonText: "حسنا"
    };
    this.imageValidation.options = {
      confirmButtonText: "حسنا"
    }
    this.loadSettings();
  }
  async loadSettings() {
    this.settings = await this.uiServicesProvider.getSettings();
    this.initEditor();
  }
  async initEditor() {
    $('#summernote').summernote({
      height: 350, // set editor height
      minHeight: null, // set minimum height of editor
      maxHeight: null, // set maximum height of editor
    });
    $('.summernote').summernote({
      airMode: true
    });
    $('#summernote').code(this.settings.infoDaTa.aboutUs);
  }

  async save() {
    // this.disableEditor();
    $('#summernote').destroy();
    console.log($('#summernote').code());
    this.spinner.show();
    try {
      const done = await this.uiServicesProvider.updateAboutUs(this.settings.id, $('#summernote').code());
      this.spinner.hide();
      if (done) {
        this.successfulDialog.show().then(value => {
          this.router.navigateByUrl("/aboutUs");
        });
      }
      else {
        this.errorDialog.show();
      }
    }
    catch (error) {
      this.spinner.hide();
      this.errorDialog.show();
    }
  }
}
