import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';


@Component({
  selector: 'app-live-stream',
  templateUrl: './live-stream.component.html',
  styleUrls: ['./live-stream.component.css']
})
export class LiveStreamComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('imageValidation') private imageValidation: SwalComponent;
  liveUrl: string = "";
  settings: Settings;
  constructor(private spinner: NgxSpinnerService, private uiServicesProvider: HomePageServicesService) {
    this.loadSettings();
  }
  async loadSettings() {
    this.settings = await this.uiServicesProvider.getSettings();
    this.liveUrl = this.settings.liveUrl;
  }
  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: "تم"
    };
    this.errorDialog.options = {
      confirmButtonText: "حسنا"
    };
    this.imageValidation.options = {
      confirmButtonText: "حسنا"
    };
  }
  async updateLiveUrl() {
    if (this.liveUrl != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateSetting(this.settings.id, "liveUrl", this.liveUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "من فضلك قم بملء رابط البث المباشر"
      }
      this.imageValidation.show();
    }
  }

}
