import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { Router } from '@angular/router';
import { ArticlesService } from 'src/app/services/uiServices/articles.service';
declare var $: any;

@Component({
  selector: 'app-articles',
  templateUrl: './articles.component.html',
  styleUrls: ['./articles.component.css']
})
export class ArticlesComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  articles: Array<Article> = [];
  constructor(private spinner: NgxSpinnerService, private router: Router, private articlesService: ArticlesService) { }

  ngOnInit() {
    this.initFootable();
    this.successfulDialog.options = {
      confirmButtonText: "تم"
    };
    this.errorDialog.options = {
      confirmButtonText: "حسنا"
    };
    this.loadArticles();
  }
  async initFootable() {
    // Row Toggler
    // -----------------------------------------------------------------
    $('#demo-foo-row-toggler').footable();

    // Accordion
    // -----------------------------------------------------------------
    $('#demo-foo-accordion').footable().on('footable_row_expanded', function (e) {
      $('#demo-foo-accordion tbody tr.footable-detail-show').not(e.row).each(function () {
        $('#demo-foo-accordion').data('footable').toggleDetail(this);
      });
    });

    // Pagination
    // -----------------------------------------------------------------
    $('#demo-foo-pagination').footable();
    $('#demo-show-entries').change(function (e) {
      e.preventDefault();
      var pageSize = $(this).val();
      $('#demo-foo-pagination').data('page-size', pageSize);
      $('#demo-foo-pagination').trigger('footable_initialized');
    });

    // Filtering
    // -----------------------------------------------------------------
    var filtering = $('#demo-foo-filtering');
    filtering.footable().on('footable_filtering', function (e) {
      var selected = $('#demo-foo-filter-status').find(':selected').val();
      e.filter += (e.filter && e.filter.length > 0) ? ' ' + selected : selected;
      e.clear = !e.filter;
    });

    // Filter status
    $('#demo-foo-filter-status').change(function (e) {
      e.preventDefault();
      filtering.trigger('footable_filter', { filter: $(this).val() });
    });

    // Search input
    $('#demo-foo-search').on('input', function (e) {
      e.preventDefault();
      filtering.trigger('footable_filter', { filter: $(this).val() });
    });



    var addrow = $('#demo-foo-addrow');
    // Search input
    $('#demo-input-search2').on('input', function (e) {
      e.preventDefault();
      addrow.trigger('footable_filter', { filter: $(this).val() });
    });

  }
  async loadArticles() {
    try {
      this.spinner.show();
      this.articles = await this.articlesService.getArticlesList();
      this.spinner.hide();
    }
    catch (errror) {
      this.spinner.hide();
      this.errorDialog.show();
    }
  }
  async changeAppearanceStatus(article: Article) {
    try {
      await this.articlesService.changeArticleAppearanceStatus(article.id, article.isShowen ? false : true);
      this.loadArticles();
    }
    catch (error) {
      this.errorDialog.show();
    }
  }
  async deleteArticle(articleId: string) {
    try {
      await this.articlesService.deleteArticle(articleId);
      this.loadArticles();
    }
    catch (error) {
      this.errorDialog.show();
    }
  }
  addNewArticle() {
    this.router.navigateByUrl("/editArticle/article/0");
  }
}
