import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';
import { Router } from '@angular/router';
declare var $: any;

@Component({
  selector: 'app-programs',
  templateUrl: './programs.component.html',
  styleUrls: ['./programs.component.css']
})
export class ProgramsComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  programs: Array<Program> = [];
  constructor(private programsService: ProgramsService, private spinner: NgxSpinnerService, private router: Router) { }

  ngOnInit() {
    this.initFootable();
    this.successfulDialog.options = {
      confirmButtonText: "تم"
    };
    this.errorDialog.options = {
      confirmButtonText: "حسنا"
    };
    this.loadPrograms();
  }
  async initFootable() {
    // Row Toggler
    // -----------------------------------------------------------------
    $('#demo-foo-row-toggler').footable();

    // Accordion
    // -----------------------------------------------------------------
    $('#demo-foo-accordion').footable().on('footable_row_expanded', function (e) {
      $('#demo-foo-accordion tbody tr.footable-detail-show').not(e.row).each(function () {
        $('#demo-foo-accordion').data('footable').toggleDetail(this);
      });
    });

    // Pagination
    // -----------------------------------------------------------------
    $('#demo-foo-pagination').footable();
    $('#demo-show-entries').change(function (e) {
      e.preventDefault();
      var pageSize = $(this).val();
      $('#demo-foo-pagination').data('page-size', pageSize);
      $('#demo-foo-pagination').trigger('footable_initialized');
    });

    // Filtering
    // -----------------------------------------------------------------
    var filtering = $('#demo-foo-filtering');
    filtering.footable().on('footable_filtering', function (e) {
      var selected = $('#demo-foo-filter-status').find(':selected').val();
      e.filter += (e.filter && e.filter.length > 0) ? ' ' + selected : selected;
      e.clear = !e.filter;
    });

    // Filter status
    $('#demo-foo-filter-status').change(function (e) {
      e.preventDefault();
      filtering.trigger('footable_filter', { filter: $(this).val() });
    });

    // Search input
    $('#demo-foo-search').on('input', function (e) {
      e.preventDefault();
      filtering.trigger('footable_filter', { filter: $(this).val() });
    });



    var addrow = $('#demo-foo-addrow');
    // Search input
    $('#demo-input-search2').on('input', function (e) {
      e.preventDefault();
      addrow.trigger('footable_filter', { filter: $(this).val() });
    });

  }

  async loadPrograms() {
    try {
      this.spinner.show();
      this.programs = await this.programsService.getProgramsList();
      this.spinner.hide();
    }
    catch (errror) {
      this.spinner.hide();
      this.errorDialog.show();
    }
  }
  async changeAppearanceStatus(program: Program) {
    try {
      await this.programsService.changeProgramAppearanceStatus(program.id, program.isShowen ? false : true);
      this.loadPrograms();
    }
    catch (error) {
      this.errorDialog.show();
    }
  }
  async deleteProgram(programId: string) {
    try {
      await this.programsService.deleteProgram(programId);
      this.loadPrograms();
    }
    catch (error) {
      this.errorDialog.show();
    }
  }
  addNewProgram() {
    this.router.navigateByUrl("/editProgram/0");
  }
}
