import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { CloudStorageProviderService } from 'src/app/services/cloud-storage-provider.service';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('imageValidation') private imageValidation: SwalComponent;
  selectedChannelLogo: any = null;
  selectedArticlesLogo: any = null;
  selectedsportsLogo: any = null;
  selectedprogramsLogo: any = null;
  selectedteamLogo: any = null;
  selectedVideoLogo: any = null;
  androidUrl: string = null;
  iosUrl: string = null;
  settings: Settings;
  programs: Array<Program>;
  selectedNewsProgramId: string = "";
  specialBanners: Array<Program> = [];
  privousSpecialBanners: Array<Program>;
  firstBannerId: string = "";
  secondBannerId: string = "";
  thirdBannerId: string = "";
  fourthBannerId: string = "";
  fivethBannerId: string = "";
  sixthBannerId: string = "";
  specialPrograms: Array<Program> = [];
  privousSpecialPrograms: Array<Program>;
  firstProgramId: string = "";
  secondProgramId: string = "";
  thirdProgramId: string = "";
  fourthProgramId: string = "";
  fivethProgramId: string = "";
  sixthProgramId: string = "";
  constructor(private spinner: NgxSpinnerService, private cloudStorage: CloudStorageProviderService, private uiServicesProvider: HomePageServicesService) {
    this.loadSettings();
    this.loadPrograms();
  }
  async loadSettings() {
    this.settings = await this.uiServicesProvider.getSettings();
    console.log("Project settings");
    console.log(this.settings);
    this.androidUrl = this.settings.infoDaTa.andUrl;
    this.iosUrl = this.settings.infoDaTa.iosUrl;
    this.specialBanners = this.settings.specialBanners.map(obj => ({ ...obj }));
    this.privousSpecialBanners = this.settings.specialBanners;
    console.log("Special banners copy");
    console.log(this.specialBanners);
    this.firstBannerId = this.specialBanners[0] ? this.specialBanners[0].id : "";
    this.secondBannerId = this.specialBanners[1] ? this.specialBanners[1].id : "";
    this.thirdBannerId = this.specialBanners[2] ? this.specialBanners[2].id : "";
    this.fourthBannerId = this.specialBanners[3] ? this.specialBanners[3].id : "";
    this.fivethBannerId = this.specialBanners[4] ? this.specialBanners[4].id : "";
    this.sixthBannerId = this.specialBanners[5] ? this.specialBanners[5].id : "";
    if (this.settings.newsPrograms) {
      this.selectedNewsProgramId = this.settings.newsPrograms.id;
    }

    this.specialPrograms = this.settings.specialPrograms.map(obj => ({ ...obj }));
    this.privousSpecialPrograms = this.settings.specialPrograms;
    console.log("Special programs copy");
    console.log(this.specialPrograms);
    this.firstProgramId = this.specialPrograms[0] ? this.specialPrograms[0].id : "";
    this.secondProgramId = this.specialPrograms[1] ? this.specialPrograms[1].id : "";
    this.thirdProgramId = this.specialPrograms[2] ? this.specialPrograms[2].id : "";
    this.fourthProgramId = this.specialPrograms[3] ? this.specialPrograms[3].id : "";
    this.fivethProgramId = this.specialPrograms[4] ? this.specialPrograms[4].id : "";
    this.sixthProgramId = this.specialPrograms[5] ? this.specialPrograms[5].id : "";
  }
  async loadPrograms() {
    this.programs = await this.uiServicesProvider.getPrograms();
  }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: "تم"
    };
    this.errorDialog.options = {
      confirmButtonText: "حسنا"
    };
    this.imageValidation.options = {
      confirmButtonText: "حسنا"
    };
  }
  async saveChannelLogo() {
    if (this.selectedChannelLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedChannelLogo, "channelLogo");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "logo", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveArticlesLogo() {
    if (this.selectedArticlesLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedArticlesLogo, "articlesLogo");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "articlesIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveSportsLogo() {
    if (this.selectedsportsLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedsportsLogo, "sportsLogo");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "sportsArticleIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveProgramsLogo() {
    if (this.selectedprogramsLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedprogramsLogo, "programsLogo");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "programIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveTeamLogo() {
    if (this.selectedteamLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedteamLogo, "teamIcon");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "teamIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveVideolLogo() {
    if (this.selectedVideoLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedVideoLogo, "videoIcon");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "videoIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  onSelectedFile(event: any, tag: string) {
    console.log("Selected file data");
    console.log(event.target.files[0].name);
    console.log(event.target.files[0]);
    switch (tag) {
      case 'channel':
        this.selectedChannelLogo = event;
        break;
      case 'articles':
        this.selectedArticlesLogo = event;
        break;
      case 'sports':
        this.selectedsportsLogo = event;
        break;
      case 'programs':
        this.selectedprogramsLogo = event;
        break;
      case 'team':
        this.selectedteamLogo = event;
        break;
      case 'video':
        this.selectedVideoLogo = event;
        break;
    }
  }
  deleteFile(tag: string) {
    switch (tag) {
      case 'channel':
        this.selectedChannelLogo = null;
        break;
      case 'articles':
        this.selectedArticlesLogo = null;
        break;
      case 'sports':
        this.selectedsportsLogo = null;
        break;
      case 'programs':
        this.selectedprogramsLogo = null;
        break;
      case 'team':
        this.selectedteamLogo = null;
        break;
      case 'video':
        this.selectedVideoLogo = null;
        break;
    }
  }
  async updateAppsUrl() {
    if (this.iosUrl && this.iosUrl != "" && this.androidUrl && this.androidUrl != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateAppsUrl(this.settings.id, this.androidUrl, this.iosUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "من فضلك قم بملء بيانات التطبيق"
      }
      this.imageValidation.show();
    }
  }
  async updateNewsProgram() {
    if (this.selectedNewsProgramId != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateNewsProgram(this.settings.id, this.selectedNewsProgramId);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "من فضلك قم باختيار برنامج الأخبار"
      }
      this.imageValidation.show();
    }
  }
  async updateSpecialBanners() {
    if (this.firstBannerId != "" && this.secondBannerId != "" && this.thirdBannerId != "" && this.fourthBannerId != "" && this.fivethBannerId != "" && this.sixthBannerId != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateSpecialBanners(this.settings.id, this.firstBannerId, this.secondBannerId, this.thirdBannerId, this.fourthBannerId, this.fivethBannerId, this.sixthBannerId, this.privousSpecialBanners);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "من فضلك قم باختيار برامج البانر المميز"
      }
      this.imageValidation.show();
    }
  }
  async updateSpecialPrograms() {
    if (this.firstProgramId != "" && this.secondProgramId != "" && this.thirdProgramId != "" && this.fourthProgramId != "" && this.fivethProgramId != "" && this.sixthProgramId != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateSpecialPrograms(this.settings.id, this.firstProgramId, this.secondProgramId, this.thirdProgramId, this.fourthProgramId, this.fivethProgramId, this.sixthProgramId, this.privousSpecialPrograms);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "من فضلك قم باختيار ستة برامج مميزة"
      }
      this.imageValidation.show();
    }
  }
}
