import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';


@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {

  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('imageValidation') private imageValidation: SwalComponent;
  phone: string = "";
  address: string = "";
  email: string = "";
  settings: Settings;
  editEnabled: boolean = false;
  constructor(private spinner: NgxSpinnerService, private uiServicesProvider: HomePageServicesService) {
  }
  async loadSettings() {
    try {
      this.spinner.show();
      this.settings = await this.uiServicesProvider.getSettings();
      this.spinner.hide();
      this.phone = this.settings.infoDaTa.phone;
      this.address = this.settings.infoDaTa.address;
      this.email = this.settings.infoDaTa.email;
    }
    catch (error) {
      console.log(error);
      this.spinner.hide();
      this.errorDialog.show();
    }

  }
  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: "تم"
    };
    this.errorDialog.options = {
      confirmButtonText: "حسنا"
    };
    this.imageValidation.options = {
      confirmButtonText: "حسنا"
    };
    this.loadSettings();

  }
  async updateContactInfo() {
    if (this.phone != "" && this.email != "" && this.address != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateContactInfo(this.settings.id, this.phone, this.email, this.address);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
          this.editEnabled = false;
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "من فضلك قم بملء بيانات الاتصال"
      }
      this.imageValidation.show();
    }
  }
  enableEdit() {
    this.editEnabled = true;
  }

}
