import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-privacy-terms',
  templateUrl: './privacy-terms.component.html',
  styleUrls: ['./privacy-terms.component.css']
})
export class PrivacyTermsComponent implements OnInit {
  privacy: any;
  settings: Settings;
  constructor(
    private sanitizer: DomSanitizer,
    private uiServicesProvider: HomePageServicesService,
    private router: Router
  ) {
    this.loadSettings();

  }

  async loadSettings() {
    this.settings = await this.uiServicesProvider.getSettings();
    this.privacy = this.sanitizer.bypassSecurityTrustHtml(this.settings.infoDaTa.policy);
  }
  ngOnInit() {
  }

  edit() {
    this.router.navigateByUrl("/editPrivacy");
  }

}
