import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-about-us',
  templateUrl: './about-us.component.html',
  styleUrls: ['./about-us.component.css']
})
export class AboutUsComponent implements OnInit {
  aboutUs: any;
  settings: Settings;
  constructor(
    private sanitizer: DomSanitizer,
    private uiServicesProvider: HomePageServicesService,
    private router: Router
  ) {
    this.loadSettings();
  }
  async loadSettings() {
    this.settings = await this.uiServicesProvider.getSettings();
    this.aboutUs = this.sanitizer.bypassSecurityTrustHtml(this.settings.infoDaTa.aboutUs);
  }
  ngOnInit() {
  }

  edit() {
    this.router.navigateByUrl("/aboutUsEdit");
  }

}
