import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';
import { NgForm } from '@angular/forms';
import { CloudStorageProviderService } from 'src/app/services/cloud-storage-provider.service';

@Component({
  selector: 'app-add-or-remove-program',
  templateUrl: './add-or-remove-program.component.html',
  styleUrls: ['./add-or-remove-program.component.css']
})
export class AddOrRemoveProgramComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  programId: string;
  isEdit: boolean;
  selectedProgram: Program = {
    name: "",
    description: "",
    duration: "",
    programDate: "",
    icon: "",
    mobBanner: "",
    playlistUrl: "",
    webBanner: "",
    isShowen: false,
    presenter: ""
  };
  selectedWebBanner: any;
  selectedMobBanner: any;
  selectedIcon: any;
  webBannerUrl: string;
  mobBannerUrl: string;
  iconUrl: string;
  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private programService: ProgramsService,
    private spinner: NgxSpinnerService,
    private cloudStorage: CloudStorageProviderService
  ) {
    this.route.params.subscribe(params => {
      this.programId = params.id;
      this.isEdit = this.programId != "0";
      console.log(this.programId);
    });
  }
  async getProgramDetails() {
    if (this.isEdit) {
      try {
        this.spinner.show();
        this.selectedProgram = await this.programService.getProgramsDetails(this.programId);
        this.webBannerUrl = this.selectedProgram.webBanner;
        this.mobBannerUrl = this.selectedProgram.mobBanner;
        this.iconUrl = this.selectedProgram.icon;

        this.spinner.hide();
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.selectedProgram = {
        name: "",
        description: "",
        duration: "",
        programDate: "",
        icon: "",
        mobBanner: "",
        playlistUrl: "",
        webBanner: "",
        isShowen: false,
        presenter: ""
      };
    }

  }
  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: "تم"
    };
    this.errorDialog.options = {
      confirmButtonText: "حسنا"
    };
    this.getProgramDetails();
  }
  onBackCliced() {
    this.location.back();
  }
  async save(form: NgForm) {
    console.log(form);
    if (form.valid) {
      if (this.webBannerUrl || this.selectedWebBanner) {
        if (this.mobBannerUrl || this.selectedMobBanner) {
          if (this.iconUrl || this.selectedIcon) {
            this.spinner.show();
            if (this.selectedWebBanner) {
              this.selectedProgram.webBanner = await this.cloudStorage.uploadFile(this.selectedWebBanner, this.selectedWebBanner.target.files[0].name);
            }
            if (this.selectedMobBanner) {
              this.selectedProgram.mobBanner = await this.cloudStorage.uploadFile(this.selectedMobBanner, this.selectedMobBanner.target.files[0].name);
            }
            if (this.selectedIcon) {
              this.selectedProgram.icon = await this.cloudStorage.uploadFile(this.selectedIcon, this.selectedIcon.target.files[0].name);
            }
            if (this.isEdit) {
              this.programService.updateProgram(this.selectedProgram).then((isDone) => {
                this.spinner.hide();
                this.successfulDialog.show().then(value => {
                  this.onBackCliced();
                });
              },
                error => {
                  this.spinner.hide();
                  this.errorDialog.show();
                });
            }
            else {
              this.programService.createProgram(this.selectedProgram).then((isDone) => {
                this.spinner.hide();
                this.successfulDialog.show().then(value => {
                  this.onBackCliced();
                });
              },
                error => {
                  this.spinner.hide();
                  this.errorDialog.show();
                });
            }


          }
          else {
            this.errorDialog.options = {
              title: "من فضلك قم باختيار الأيكون",
              confirmButtonText: "حسنا"
            };
            this.errorDialog.show();
          }
        }
        else {
          this.errorDialog.options = {
            title: "من فضلك قم باختيار بانر التطبيق",
            confirmButtonText: "حسنا"
          };
          this.errorDialog.show();
        }
      }
      else {
        this.errorDialog.options = {
          title: "من فضلك قم باختيار بانر الموقع",
          confirmButtonText: "حسنا"
        };
        this.errorDialog.show();
      }

    }
    else {
      this.errorDialog.options = {
        title: "من فضلك قم بملء بيانات البرنامج",
        confirmButtonText: "حسنا"
      };
      this.errorDialog.show();
    }
  }
  onSelectedFile(event: any, tag: string) {
    console.log("Selected file data");
    console.log(event.target.files[0].name);
    console.log(event.target.files[0]);
    switch (tag) {
      case 'web':
        this.selectedWebBanner = event;
        break;
      case 'mob':
        this.selectedMobBanner = event;
        break;
      case 'icon':
        this.selectedIcon = event;
        break;
    }
  }
  deleteFile(tag: string) {
    switch (tag) {
      case 'web':
        this.selectedWebBanner = null;
        break;
      case 'mob':
        this.selectedMobBanner = null;
        break;
      case 'icon':
        this.selectedIcon = null;
        break;
    }
  }
}
